* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html,
body,
#root,
#root > div {
  height: 100%;
  margin: 0;
  padding: 0;
}

#navbar {
    width: 100%;
    height: 100px;
    background: rgba(75, 12, 0);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    position: fixed;
    top: 0;
    z-index: 1;
}

#navbarLeft {
    float: left;
}

#navbarMiddle {
    text-align: center;
}

#navbarRight {
    float: right;
}

#navBarPic {
    height: 70px;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 25px;
    float: left;
}

.navItem {
    display: inline-block;
    width: 33%;
}


#guess {
    color: white;
    font-size: 25px;
    font-family: 'MedievalSharp', cursive;
    margin-top: 40px;
}

.score {
    color: white;
    font-size: 15px;
    font-family: 'MedievalSharp', cursive;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: right;
    margin-right: 25px;
    margin-top: 10px;
}


#header {
    margin-top: 100px;
    width: 100%;
    height: 400px;
    background-image: linear-gradient(rgb(75, 12, 0), rgb(248, 248, 252), rgba(75, 12, 0));
    }

#instructions {
    color: rgb(8, 8, 8);
    font-family: 'MedievalSharp', cursive;
    padding-top: 100px;
    font-size: 25px;
    font-weight: bold;
    float: center;
}

.headerSection {
    width: 33%;
    display: inline-block;
}

#headerLeft {
    float: left;
}

#headerMiddle {
    text-align: center;
}

#headerRight {
    float: right;
}

#GoT {
    height: 200px;
    float: left;
    margin-top: 100px;
    margin-left: 5%;
}

#GoT2 {
    height: 200px;
    float: right;
    margin-top: 100px;
    margin-left: 5%;
}


.container {
  display: -webkit-flex;
  display: flex;
  min-height: 80%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 100%;
  margin: 0 auto;
}


.click-item {
    background-size: cover;
    background-position: center;
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid rgb(241, 241, 241);
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  }
  
  .click-item:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    transition: 0.1s;
  }
  
  .characters {
    height: 200px;
    border-radius: 5px;
    opacity: 0.8;
    transition: all 1s;
  }

  .characters:hover {
    opacity: .9;
  }

  .characterBox {
    padding: 20px;
    margin: 20px;
    border: solid rgb(202, 46, 46) 1px;
    border-radius: 5px;
    background-color: rgb(253, 0, 0);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    transition: all 1s;
  }

  .characterBox:hover {
    background-color: rgb(250, 248, 248);
  }
#footer {
    bottom: 0;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(rgb(75, 12, 0), rgb(248, 248, 252), rgba(75, 12, 0));
}
.footerWrapper {
    margin-top: 30px;   
}
